.accent-button-style {
  padding: 5px 15px;
  border-radius: 15px;
  cursor: pointer;
  background-color: get-color('accent-color');
  color: get-color('primary-text-color');
  font-weight: bolder;

  &:hover,
  &:focus {
    background-color: get-color('accent-color-hover');
    transition: background-color $transition-time $transition-time-function;
  }
}

.menu-toggle {
  background: none;
  fill: getColor('primary-text-color');
  transition: fill $transition-time $transition-time-function;
}

.mode-switch__btn {
  background: none;
  width: 24px;
  height: 24px;
  fill: getColor('primary-text-color');
  transition: fill $transition-time $transition-time-function;
  position: fixed;
  bottom: 4px;
  left: 4px;
}

.backToTopBtn {
  display: none;
  position: fixed;
  top: 20px;
  right: 50%;
  transform: translateX(50%);
  z-index: 10000;
  @extend .accent-button-style;
}

.submit-btn {
  background-color: getColor('secondary-btn-color');
  border: 1px solid getColor('secondary-btn-color');
  border-radius: 100px;
  padding: 5px;
  transition: background-color $transition-time $transition-time-function,
      border-color $transition-time $transition-time-function;
}

.item-delete__btn {
  border: none;
  background-color: transparent;
}

.clear-history__btn {
  background-color: getColor('secondary-btn-color');
  border: 1px solid getColor('secondary-btn-color');
  padding: 3px;
  border-radius: 50px;
  font-family: getFontFamily('primary-font');
  font-size: getFontSize('small-font');
  margin-bottom: 5px;
  width: fit-content;
  transition: background-color $transition-time $transition-time-function,
    border-color $transition-time $transition-time-function;
}

@media screen and (min-width: 480px) {

  .clear-history__btn {
    font-size: getFontSize('medium-font');
  }
}

@media screen and (min-width: 768px) {
  .mode-switch__btn {
    width: 35px;
    height: 35px;
  }

  .mobile-menu__container {
    display: none;
  }
}

@media screen and (min-width: 1200px) {

  .submit-btn {
    padding: 10px;
  }

  .clear-history__btn {
    padding: 8px;
    font-size: getFontSize('x-large-font');
  }
}