img {
  max-width: 100%;
  aspect-ratio: 3 / 2;
  object-fit: cover;
}

ul {
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover, a:focus {
  transform: scale(1.1);
}

button {
  border-style: hidden;
}

button:hover, button:focus {
  transform: scale(1.1);
}

body.dark-mode {
  background-color: #000;
}

body.dark-mode .header-section {
  background-color: #6096b4;
}

body.dark-mode .light-mode__icon {
  opacity: 0;
}

body.dark-mode .dark-mode__icon {
  opacity: 1;
}

body.dark-mode .light-dark-theme-btn__title {
  color: #bebebe;
}

body.dark-mode .mode-switch__btn {
  fill: #bebebe;
}

body.dark-mode .form-details__container .form-details__container {
  background-color: rgba(141, 163, 175, .6);
}

body.dark-mode .form-details__container .search-form, body.dark-mode .form-details__container input {
  background-color: #94abb8;
}

body.dark-mode .submit-btn {
  background-color: #426a80;
  border: 2px solid #426a80;
}

body.dark-mode .gallery .photo-card {
  background-color: #94abb8;
  border: 2px solid #94abb8;
}

body.dark-mode .clear-history__btn {
  background-color: #426a80;
  border: 1px solid #426a80;
}

.accent-button-style, .backToTopBtn {
  cursor: pointer;
  background-color: get-color("accent-color");
  color: get-color("primary-text-color");
  border-radius: 15px;
  padding: 5px 15px;
  font-weight: bolder;
}

.accent-button-style:hover, .backToTopBtn:hover, .accent-button-style:focus, .backToTopBtn:focus {
  background-color: get-color("accent-color-hover");
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1);
}

.menu-toggle {
  fill: #000;
  background: none;
  transition: fill 2s cubic-bezier(.4, 0, .2, 1);
}

.mode-switch__btn {
  width: 24px;
  height: 24px;
  fill: #000;
  background: none;
  transition: fill 2s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: 4px;
  left: 4px;
}

.backToTopBtn {
  z-index: 10000;
  display: none;
  position: fixed;
  top: 20px;
  right: 50%;
  transform: translateX(50%);
}

.submit-btn {
  background-color: rgba(124, 171, 197, .6);
  border: 1px solid rgba(124, 171, 197, .6);
  border-radius: 100px;
  padding: 5px;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1), border-color 2s cubic-bezier(.4, 0, .2, 1);
}

.item-delete__btn {
  background-color: rgba(0, 0, 0, 0);
  border: none;
}

.clear-history__btn {
  width: fit-content;
  background-color: rgba(124, 171, 197, .6);
  border: 1px solid rgba(124, 171, 197, .6);
  border-radius: 50px;
  margin-bottom: 5px;
  padding: 3px;
  font-family: Montserrat;
  font-size: 12px;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1), border-color 2s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 480px) {
  .clear-history__btn {
    font-size: 14px;
  }
}

@media screen and (min-width: 768px) {
  .mode-switch__btn {
    width: 35px;
    height: 35px;
  }

  .mobile-menu__container {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .submit-btn {
    padding: 10px;
  }

  .clear-history__btn {
    padding: 8px;
    font-size: 18px;
  }
}

.header__contacts-container, .location-item {
  display: none;
}

@media screen and (min-width: 768px) {
  .header__contacts-container {
    display: flex;
  }

  .header__contacts-container .header__contacts-list {
    flex-direction: column;
    margin: 15px 0;
    display: flex;
  }

  .header__contacts-container .header__contacts-link {
    align-items: center;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 300;
    transition: color 2s cubic-bezier(.4, 0, .2, 1);
    display: flex;
  }

  .header__contacts-container .header__contacts-item:not(:last-child) {
    padding-bottom: 10px;
  }

  .header__contacts-container .header__contacts-icon {
    fill: currentColor;
    margin-right: 10px;
  }
}

@media screen and (min-width: 1200px) {
  .header__contacts-container {
    display: flex;
  }

  .header__contacts-container .header__contacts-list {
    flex-direction: row;
    margin: 25px 0;
    display: flex;
  }

  .header__contacts-container .location-item {
    display: flex;
  }

  .header__contacts-container .header__contacts-link {
    align-items: center;
    font-family: Montserrat;
    font-size: 18px;
    font-weight: 300;
    transition: color 2s cubic-bezier(.4, 0, .2, 1);
    display: flex;
  }

  .header__contacts-container .header__contacts-item:not(:last-child) {
    margin-right: 20px;
    padding: 0;
  }

  .header__contacts-container .header__contacts-icon {
    fill: currentColor;
    margin-right: 10px;
  }
}

.form-details__section {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.form-details__section .form-details__container {
  width: fit-content;
  z-index: 2;
  background-color: rgba(189, 205, 214, .6);
  border-radius: 20px;
  flex-direction: column;
  align-items: center;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.form-details__section .form-details__container .search-form {
  background-color: #bdcdd6;
  border-radius: 30px;
  padding-left: 10px;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 4px 6px -6px #222;
}

.form-details__section .form-details__container input {
  width: 100px;
  background-color: #bdcdd6;
  border: none;
  outline: none;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1);
}

.form-details__section .form-details__container input::-ms-input-placeholder {
  font-family: Montserrat;
  font-size: 12px;
}

.form-details__section .form-details__container input::placeholder {
  font-family: Montserrat;
  font-size: 12px;
}

.form-details__section .is-hidden {
  display: none;
}

.form-details__section .is-visible {
  border-top: none;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  margin-top: 5px;
  display: flex;
}

.form-details__section .is-visible .search-history__list {
  cursor: default;
  width: 100%;
  flex-direction: column;
  align-items: flex-start;
  padding-left: 0;
  display: flex;
}

.form-details__section .is-visible .search-history__item {
  width: 100%;
  justify-content: space-between;
  margin-bottom: 5px;
  font-size: 12px;
  display: flex;
}

.form-details__section .is-visible .search-item__text {
  cursor: pointer;
  margin-bottom: 0;
}

.form-details__section .is-visible .search-item__text:hover, .form-details__section .is-visible .search-item__text:focus {
  transform: scale(1.1);
}

@media screen and (min-width: 480px) {
  .form-details__section .form-details__container input {
    width: auto;
  }

  .form-details__section .form-details__container input::-ms-input-placeholder {
    font-size: 14px;
  }

  .form-details__section .form-details__container input::placeholder {
    font-size: 14px;
  }

  .form-details__section .is-visible {
    width: 150px;
  }

  .form-details__section .search-history__item {
    font-size: 14px;
  }
}

@media screen and (min-width: 768px) {
  .form-details__section .form-details__container input {
    width: auto;
  }

  .form-details__section .form-details__container input::-ms-input-placeholder {
    font-size: 16px;
  }

  .form-details__section .form-details__container input::placeholder {
    font-size: 16px;
  }

  .form-details__section .is-visible {
    width: 150px;
    font-size: 16px;
  }

  .form-details__section .is-visible .search-history__item {
    font-size: 16px;
  }
}

@media screen and (min-width: 1200px) {
  .form-details__section .form-details__container input {
    width: 250px;
  }

  .form-details__section .form-details__container input::-ms-input-placeholder {
    font-size: 18px;
  }

  .form-details__section .form-details__container input::placeholder {
    font-size: 18px;
  }

  .form-details__section .is-visible {
    width: 200px;
    font-size: 18px;
  }

  .form-details__section .is-visible .search-history__item {
    font-size: 18px;
  }
}

.gallery {
  width: fit-content;
  margin-top: 40px;
}

.gallery .gallery__photo-card-container {
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 5px;
  display: flex;
}

.gallery .photo-card {
  width: 120px;
  background-color: #bdcdd6;
  border: 2px solid #bdcdd6;
  border-radius: 0 0 15px 15px;
  margin-top: 5px;
  padding-bottom: 3px;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1), border-color 2s cubic-bezier(.4, 0, .2, 1);
}

.gallery .photo-card:hover, .gallery .photo-card:focus {
  transform: scale(1.1);
}

.gallery .info {
  justify-content: space-evenly;
  margin-top: 3px;
  margin-bottom: 0;
  display: flex;
}

.gallery .info-item {
  flex-direction: column;
  align-items: center;
  margin-bottom: 0;
  font-family: Nunito;
  font-size: 5px;
  display: flex;
}

@media screen and (min-width: 480px) {
  .gallery .photo-card {
    width: 200px;
  }

  .gallery .info-item {
    margin-right: 0;
    font-size: 9px;
  }
}

@media screen and (min-width: 768px) {
  .gallery .photo-card {
    width: 250px;
  }

  .gallery .info-item {
    font-size: 12px;
  }
}

@media screen and (min-width: 1200px) {
  .gallery {
    margin-top: 50px;
  }

  .gallery .gallery__photo-card-container {
    gap: 20px;
  }
}

.logo-container {
  align-items: center;
  display: flex;
}

.logo-text {
  color: #000;
  margin-bottom: 0;
  font-family: MuesoModerno;
  font-size: 16px;
  transition: color 2s cubic-bezier(.4, 0, .2, 1);
}

.logo__image-icon {
  fill: #000;
  width: 18px;
  height: 18px;
  transition: fill 2s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 480px) {
  .logo-text {
    font-size: 18px;
  }
}

@media screen and (min-width: 768px) {
  .logo-text {
    font-size: 22px;
  }

  .logo__image-icon {
    width: 22px;
    height: 22px;
  }
}

@media screen and (min-width: 1200px) {
  .logo-text {
    font-size: 32px;
  }

  .logo__image-icon {
    width: 30px;
    height: 30px;
  }
}

.mobile__menu-container {
  display: flex;
}

.menu .link:hover, .menu .link:focus {
  text-decoration: underline;
}

.menu-container {
  width: 100vw;
  height: 100vh;
  z-index: 999;
  background-color: #fff;
  padding: 32px;
  transition: all 2s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.menu-container.is-open {
  transform: translateX(0);
}

.header__social-container {
  display: none;
}

.header__social-container .social-icon {
  transition: fill 2s cubic-bezier(.4, 0, .2, 1);
}

.header__social-container .social-icon:hover, .header__social-container .social-icon:focus {
  transform: scale(1.1);
}

@media screen and (min-width: 768px) {
  .header__social-container {
    display: flex;
  }

  .header__social-list {
    gap: 40px;
    margin-bottom: 0;
    display: flex;
  }

  .header__social-list .social-icon {
    width: 25px;
    height: 25px;
    fill: currentColor;
  }
}

@media screen and (min-width: 1200px) {
  .header__social-container {
    display: flex;
  }

  .header__social-list {
    gap: 60px;
    margin-bottom: 0;
    display: flex;
  }

  .header__social-list .social-icon {
    fill: #000;
    width: 35px;
    height: 35px;
    display: flex;
  }
}

.light-dark-theme__container {
  z-index: 1000;
  justify-content: center;
  align-items: flex-start;
  display: flex;
  position: absolute;
  top: 4px;
}

.light-dark-theme__container .light-dark-theme-btn__title {
  transform-origin: 0 0;
  font-family: Montserrat;
  font-size: 12px;
  position: fixed;
  bottom: -2px;
  left: 8px;
  transform: rotate(270deg);
}

.light-dark-theme__container .mode-icon__container {
  --rotation: 0;
  height: 100vmin;
  pointer-events: none;
  transform: rotate(calc(var(--rotation) * 1deg) );
  justify-content: center;
  transition: transform 2s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.light-dark-theme__container .mode-icons {
  width: 23px;
  height: 23px;
  fill: #000;
  transition: opacity 2s cubic-bezier(.4, 0, .2, 1), fill 2s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

.light-dark-theme__container .light-mode__icon {
  opacity: 1;
  top: 0;
}

.light-dark-theme__container .dark-mode__icon {
  opacity: 0;
  bottom: 0;
  transform: rotate(180deg);
}

@media screen and (min-width: 768px) {
  .light-dark-theme__container .mode-icons {
    width: 35px;
    height: 35px;
  }

  .light-dark-theme__container .light-dark-theme-btn__title {
    font-size: 16px;
    bottom: 5px;
    left: 11px;
  }
}

.header-section {
  color: #000;
  background-color: #93bfcf;
  border-radius: 0 0 20px 20px;
  justify-content: center;
  align-items: center;
  margin-bottom: 10px;
  padding: 30px 0 15px;
  transition: background-color 2s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.header-section .header-content__container {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header-section {
    padding: 25px 0;
  }
}

.main-container {
  flex-direction: column;
  align-items: center;
  padding: 0 25px;
  display: flex;
}

@media screen and (min-width: 480px) {
  .main-container {
    padding: 0 35px;
  }
}

body.hidden-scroll {
  overflow: hidden;
}

.sl-overlay {
  z-index: 1035;
  background: #fff;
  display: none;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.sl-wrapper {
  z-index: 1040;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.sl-wrapper * {
  box-sizing: border-box;
}

.sl-wrapper button {
  cursor: pointer;
  background: none;
  border: 0;
  padding: 0;
  font-size: 28px;
}

.sl-wrapper button:hover {
  opacity: .7;
}

.sl-wrapper .sl-close {
  z-index: 10060;
  height: 44px;
  width: 44px;
  color: #000;
  margin-top: -14px;
  margin-right: -14px;
  font-family: Arial, Baskerville, monospace;
  font-size: 3rem;
  line-height: 44px;
  display: none;
  position: fixed;
  top: 30px;
  right: 30px;
}

.sl-wrapper .sl-counter {
  z-index: 10060;
  color: #000;
  font-size: 1rem;
  display: none;
  position: fixed;
  top: 30px;
  left: 30px;
}

.sl-wrapper .sl-download {
  width: 100%;
  text-align: center;
  z-index: 10060;
  color: #fff;
  font-size: 1rem;
  display: none;
  position: fixed;
  bottom: 5px;
}

.sl-wrapper .sl-download a {
  color: #fff;
}

.sl-wrapper .sl-navigation {
  width: 100%;
  display: none;
}

.sl-wrapper .sl-navigation button {
  height: 44px;
  width: 22px;
  text-align: center;
  z-index: 10060;
  color: #000;
  margin-top: -22px;
  font-family: Arial, Baskerville, monospace;
  line-height: 44px;
  display: block;
  position: fixed;
  top: 50%;
}

.sl-wrapper .sl-navigation button.sl-next {
  font-size: 2rem;
  right: 5px;
}

.sl-wrapper .sl-navigation button.sl-prev {
  font-size: 2rem;
  left: 5px;
}

@media (min-width: 35.5em) {
  .sl-wrapper .sl-navigation button {
    width: 44px;
  }

  .sl-wrapper .sl-navigation button.sl-next {
    font-size: 3rem;
    right: 10px;
  }

  .sl-wrapper .sl-navigation button.sl-prev {
    font-size: 3rem;
    left: 10px;
